/* Base Styles */
:root {
  --primary-color: #4361ee;
  --secondary-color: #3f37c9;
  --accent-color: #4cc9f0;
  --dark-color: #1a1a2e;
  --light-color: #f8f9fa;
  --success-color: #4bb543;
  --error-color: #ff3333;
  --gray-color: #6c757d;
  --light-gray: #e9ecef;
  --shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --transition: all 0.3s ease;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #000000;
  color: #333;
}

/* App Layout */
.ecommerce-app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app-header {
  display: flex;
  margin-top: 120px;
  justify-content: space-between;
  align-items: center;  
  padding: 1rem 2rem;
  background-color: rgb(232, 163, 67);
  box-shadow: var(--shadow);
  position: sticky;
  top: 0;
  z-index: 100;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary-color);
}

.main-content {
  display: flex;
  flex: 1;
}

.filters-sidebar {
  width: 250px;
  padding: 1.5rem;
  background-color: rgb(0, 0, 0);
  box-shadow: var(--shadow);
  height: calc(100vh - 70px);
  position: sticky;
  top: 70px;
  overflow-y: auto;
}

.filter-section {
  margin-bottom: 1.5rem;
}

.filter-section h4 {
  margin-bottom: 0.75rem;
  font-size: 1rem;
  color: var(--gray-color);
}

.price-range {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.price-range input[type="range"] {
  width: 100%;
}

.product-grid {
  flex: 1;
  padding: 2rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 2rem;
}

/* Search */
.search-container {
  position: relative;
  width: 40%;
}

.search-container input {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 2.5rem;
  border: 1px solid var(--light-gray);
  border-radius: 50px;
  font-size: 1rem;
  transition: var(--transition);
}

.search-container input:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(67, 97, 238, 0.2);
}

.search-icon {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  color: var(--gray-color);
}

/* Product Card */
.product-card {
  background-color: rgb(39, 37, 37);
  border-radius: 8px;
  overflow: hidden;
  box-shadow: whitesmoke;
  transition: var(--transition);
  cursor: pointer;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(255, 255, 255, 0.272);
}

.product-image-container {
  position: relative;
  height: 200px;
  overflow: hidden;
}

.product-image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: var(--transition);
}

.product-card:hover .product-image-container img {
  transform: scale(1.05);
}

.premium-badge {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: rgb(255, 170, 0);
  color: rgb(255, 255, 255);
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.75rem;
  font-weight: 600;
}

.product-info {
  padding: 1rem;
}

.product-info h3 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.price {
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 0.5rem;
}

.rating {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  margin-bottom: 1rem;
}

.rating .star {
  color: #ffc107;
}

.rating span {
  font-size: 0.8rem;
  color: var(--gray-color);
  margin-left: 0.25rem;
}

.add-to-cart-btn {
  width: 100%;
  padding: 0.5rem;
  background-color: rgba(188, 143, 7, 0.886);
  color: white;
  border: none;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
}

.add-to-cart-btn:hover {
  background-color: var(--secondary-color);
}

/* Cart Styles */
.cart-icon-btn {
  position: relative;
  background: none;
  border: none;
  font-size: 1.25rem;
  color: var(--dark-color);
  cursor: pointer;
  padding: 0.5rem;
}

.cart-badge {
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: var(--error-color);
  color: white;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.7rem;
  font-weight: 600;
}

.cart-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  display: flex;
  justify-content: flex-end;
}

.cart-sidebar {
  width: 400px;
  height: 100%;
  background-color: white;
  display: flex;
  flex-direction: column;
}

.cart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid var(--light-gray);
}

.close-cart {
  background: none;
  border: none;
  font-size: 1.25rem;
  cursor: pointer;
  color: var(--gray-color);
}

.empty-cart {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
  gap: 1rem;
  color: var(--gray-color);
}

.continue-shopping-btn {
  padding: 0.75rem 1.5rem;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
}

.cart-items {
  flex: 1;
  overflow-y: auto;
  padding: 1rem;
}

.cart-item {
  display: flex;
  gap: 1rem;
  padding: 1rem 0;
  border-bottom: 1px solid var(--light-gray);
}

.cart-item img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 4px;
}

.item-details {
  flex: 1;
}

.item-details h4 {
  font-size: 0.9rem;
  margin-bottom: 0.25rem;
}

.quantity-controls {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-top: 0.5rem;
}

.quantity-btn {
  width: 25px;
  height: 25px;
  border: 1px solid var(--light-gray);
  background: none;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.remove-item-btn {
  background: none;
  border: none;
  color: var(--gray-color);
  cursor: pointer;
  align-self: flex-start;
}

.cart-summary {
  padding: 1.5rem;
  border-top: 1px solid var(--light-gray);
}

.summary-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.summary-row.total {
  font-weight: 700;
  font-size: 1.1rem;
  margin: 1rem 0;
}

.checkout-btn {
  width: 100%;
  padding: 1rem;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 4px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: var(--transition);
}

.checkout-btn:hover {
  background-color: var(--secondary-color);
}

.checkout-btn:disabled {
  background-color: var(--gray-color);
  cursor: not-allowed;
}

.error-message {
  color: var(--error-color);
  margin-top: 1rem;
  text-align: center;
}

.success-message {
  color: var(--success-color);
  margin-top: 1rem;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

/* Product Modal */
.product-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.product-modal {
  background-color: white;
  border-radius: 8px;
  width: 90%;
  max-width: 900px;
  max-height: 90vh;
  overflow: hidden;
  position: relative;
}

.close-modal {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  z-index: 10;
  color: var(--gray-color);
}

.modal-content {
  display: flex;
  height: 100%;
}

.modal-image {
  flex: 1;
  padding: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f9f9f9;
}

.modal-image img {
  max-width: 100%;
  max-height: 400px;
  object-fit: contain;
}

.modal-details {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
}

.modal-details h2 {
  font-size: 1.75rem;
  margin-bottom: 1rem;
}

.modal-details .price {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.modal-details .rating {
  margin-bottom: 1.5rem;
}

.modal-details .description {
  margin-bottom: 2rem;
  line-height: 1.6;
  color: var(--gray-color);
}

.modal-actions {
  display: flex;
  gap: 1rem;
}

.buy-now-btn {
  flex: 1;
  padding: 1rem;
  background-color: var(--secondary-color);
  color: white;
  border: none;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
}

.buy-now-btn:hover {
  background-color: #2a2a8f;
}

/* Responsive */
@media (max-width: 768px) {
  .main-content {
    flex-direction: column;
  }
  
  .filters-sidebar {
    width: 100%;
    height: auto;
    position: static;
  }
  
  .modal-content {
    flex-direction: column;
  }
  
  .modal-image {
    padding: 1rem;
  }
  
  .modal-details {
    padding: 1.5rem;
  }
  
  .cart-sidebar {
    width: 100%;
  }
}

/* Payment Form Styles */
.payment-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
}

.payment-form-container {
  background-color: white;
  padding: 2rem;
  border-radius: 8px;
  width: 100%;
  max-width: 500px;
  position: relative;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.close-payment-form {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--gray-color);
}

.payment-form-container h3 {
  margin-bottom: 1rem;
  color: var(--dark-color);
}

.payment-form-container p {
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--primary-color);
}

.card-element-container {
  padding: 1rem;
  border: 1px solid var(--light-gray);
  border-radius: 4px;
  margin-bottom: 1.5rem;
}

.submit-payment-btn {
  width: 100%;
  padding: 1rem;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 4px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: var(--transition);
}

.submit-payment-btn:hover {
  background-color: var(--secondary-color);
}

.submit-payment-btn:disabled {
  background-color: var(--gray-color);
  cursor: not-allowed;
}

.payment-error {
  color: var(--error-color);
  margin-bottom: 1rem;
  text-align: center;
}

/* Payment Success Notification */
.payment-success-notification {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background-color: var(--success-color);
  color: white;
  padding: 1rem 1.5rem;
  border-radius: 4px;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  box-shadow: var(--shadow);
  z-index: 1000;
  animation: slideIn 0.3s ease-out;
}

.success-icon {
  font-size: 1.2rem;
}

@keyframes slideIn {
  from {
    transform: translateY(100px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}